@font-face {
    font-family: "a";
    src: url("../src/fonts/free_font_12.ttf");
    font-weight: bold;
}
@font-face {
    font-family: "b";
    src: url("../src/fonts/ae_Metal.ttf");
    font-weight: bold;
}
@font-face {
    font-family: "c";
    src: url("../src/fonts/virgo.ttf");
    font-weight: bold;
}

:root {
     --back-color : #eeeff3;
    --font-color : #292930;
    --box-shadow-1:#000000;
    --change-color: #6379FF;
    --color-of-transparent: #c6c6c6;
    --of-white: #6e6e6e;
    --first-color: #6379FF;
    --secode-color: #16A085;
    --third-color: #EA384D;
    --forth-color: #FF8008;
    --fifth-color: #FAACA8;
    --preview-color:#16A085;
}
.App {
    text-align: center;
}

.navbar {
    display: flex;
    justify-content: space-between;
    align-items: center;
    background: linear-gradient(270deg, #f78da7, #8fccf3);
}
.navbar .logo {
    margin: 10px;
    padding: 0;
}
.navbar .logo h2 {
    font-family: c;
}
.navbar .logo h2 .change-color {
    font-family: c;
    color: #fff;
}
.navbar a {
    text-decoration: none;
    margin: 10px 20px;
    font-size: 2rem;
    color: #fff;
}
h1 {
    font-family: b;
}

.carousel {
    overflow: hidden;
}
.inner-carousel {
    display: flex;
    justify-content: flex-start;
    align-items: center;
}
.item {
    width: 200px;
    height: 300px;
    min-width: 200px;
    min-height: 300px;
    margin: 20px;
    position: relative;
    overflow: hidden;
    border-radius: 10px;
}
.item img {
    width: 100%;
    height: 100%;
    border-radius: 10px;
    pointer-events: none;
    overflow: hidden;
}
.item .headline {
    position: absolute;
    bottom: 0;
    left: 0;
    background: #ddddddd3;
    width: 100%;
    height: 100%;
    border-radius: 10px;
    transform: translate(0%, 100%);
    transition: 0.5s;
    display: flex;
    align-items: center;
    justify-content: center;
}
.item .headline a {
    font-size: 3rem;
    transition: 0.5s;
    margin: 10px;
    padding: 10px;
    text-decoration: none;
    color: #000;
}
item .headline .preview {
    transform: translate(0%, 150%);
    background: #d36aea;
    padding: 10px;
}
item .headline .link {
    transform: translate(0%, 250%);
}
.item:hover .headline {
    transform: translate(0, 0);
    padding: 10px;
    transition: all 0.5s;
}
.item:hover .headline .link {
    transform: translate(0, 0);
}
.item:hover .headline .preview {
    transform: translate(0, 0);
}

.list_work {
    margin: 0;
    padding: 10px;
}
.list_work ul {
    margin: 0;
    padding: 0;
    display: flex;
    justify-content: center;
    align-items: center;
}
.list_work ul li {
    list-style: none;
    margin: 10px;
    padding: 10px;
    font-size: 2.3rem;
    font-family: c;
    border: 2px solid #000;
    border-radius: 15px;
    text-decoration: none;
}
.list_work ul li .link {
    text-decoration: none;
    color: #000;
}

.container {
    width: 100%;
    height: 500px;
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    gap: 20px;
    z-index: 999;
}
.container .img {
    width: 400px;
    height: 400px;
    position: relative;
    margin: 10px;
}
.container .img:before {
    width: 100%;
    height: 100%;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    content: "";
    background: #475ced;
    border-radius: 30% 70% 69% 31% / 40% 24% 76% 60%;
    z-index: -1;
}

.container .img img {
    width: 300px;
    height: 400px;
}
.container .content {
    font-family: b;
    margin: 10px 20px;
}
.container .content .link {
    margin-left: 10px;
    padding: 10px;
    font-size: 2.3rem;
    font-family: c;
    border: 1px solid #000;
    border-radius: 5px;
    text-decoration: none;
    color: #000;
    transition: 0.5s;
    position: relative;
}
.container .content .link:nth-child(5) {
    color: #fff;
}
.container .content .link:nth-child(5):before {
    position: absolute;
    top: 0;
    left: 0;
    content: "";
    width: 100%;
    height: 100%;
    background: #272727;
    color: #fff;
    z-index: -1;
}

.popup {
    position: fixed;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: 100%;
    height: 100vh;
    background: #2d2d2de5;
    z-index: 9999;
    display: block;
    transition: all 0.3s;
}
.popup.visible {
    display: block;
}
.popup video {
    width: 100%;
    height: 100%;
}
.popup p {
    position: absolute;
    top: 10%;
    right: 0;
    width: 30px;
    height: 30px;
    line-height: 40px;
    transform: translate(-50%, -50%);
    border: 1px solid #fff;
    padding: 10px;
    border-radius: 50%;
}
.popup p i {
    font-size: 1.5rem;
    color: #fff;
}

/* =======skills Style==========*/ 
.skilles {
        margin: 0;
        padding: 0;
    }
.skilles h1{
        text-align: center;
        color: var(--of-white);
        margin: 40px 0;
    }
.skilles .content-box{
        display: flex;
        align-items: center;
        justify-content: center;
    }
.skilles .content-box .box.active{
    box-shadow: rgba(50, 50, 93, 0.25) 0px 13px 27px -5px, rgba(0, 0, 0, 1) 0px 8px 16px -8px;

}
.skilles .content-box .box{
    width: 400px;
    height: 100px;
    color: var(--of-white);
    display: flex;
    align-items: center;
    position: relative;
    text-align: left;
    font-size: 30px;
    margin: 10px 20px;
    border-radius: 10px;
    box-shadow: rgba(0, 0, 0, 0.1) 0px 1px 3px 0px, rgba(0, 0, 0, 0.06) 0px 1px 2px 0px;
    overflow: hidden;
}

.skilles .content-box .box:before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 3px;
    height: 100%;
    background: #0063B1;
    z-index: -1;
    border-radius: 10px;
    box-shadow: rgba(0, 0, 0, 0.25) 0px 1px 54px 10px, rgba(0, 0, 0, 0.12) 0px -12px 30px, rgba(0, 0, 0, 0.12) 0px 4px 6px, rgba(0, 0, 0, 0.17) 0px 12px 13px, rgba(0, 0, 0, 0.09) 0px -3px 5px;
    transition: 0.5s;
    transform: translateX(-10px);
}
.skilles .content-box-2 .box:before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 3px;
    height: 100%;
    background: #0063B1;
    z-index: -1;
    border-radius: 10px;
    box-shadow: rgba(0, 0, 0, 0.25) 0px 1px 54px 10px, rgba(0, 0, 0, 0.12) 0px -12px 30px, rgba(0, 0, 0, 0.12) 0px 4px 6px, rgba(0, 0, 0, 0.17) 0px 12px 13px, rgba(0, 0, 0, 0.09) 0px -3px 5px;
    transition: 0.5s;
    transform: translateX(-10px);
}
.skilles .content-box .box:nth-child(2):before{background:var(--secode-color);}
.skilles .content-box .box:nth-child(3):before{background:var(--third-color);}
.skilles .content-box-2 .box:nth-child(1):before{background:var(--ford-color);}
.skilles .content-box-2 .box:nth-child(2):before{background:var(--fifth-color);}
.skilles .content-box .box:hover:before{
    transform: translateX(320px);
}
.skilles .content-box-2 .box:hover:before{
    transform: translateX(320px);
    
}
.skilles .content-box .box i{
    margin: 10px 20px;
}
.skilles .content-box .box:nth-child(1) i{
    color: var(--first-color);
}
.skilles .content-box .box:nth-child(2) i{
    color: var(--secode-color);
}
.skilles .content-box .box:nth-child(3) i{
    color: var(--third-color);
}
.skilles .content-box .box:nth-child(1) span{
    background: var(--first-color);
}
.skilles .content-box .box:nth-child(2) span{
    background: var(--secode-color);
}
.skilles .content-box .box:nth-child(3) span{
    background: var(--third-color);
}
.skilles .content-box .box span{
    position: absolute;
    top: 0;
    left: 0;
    width: 5px;
    height: 100%;
    background: var(--change-color);
    border-radius: 10px;
}
/* skilles content-box-2 */
.skilles .content-box-2{
        display: flex;
