@media (max-width: 640px) {
    .navbar {
        justify-content: space-between;
    }
    .navbar a {
        margin: 10px;
    }
    .navbar .logo img {
        width: 50px;
        height: 50px;
    }

    .item {
        width: 100px;
        height: 160px;
    }
    .item .headline a {
        font-size: 1.3rem;
    }

    .list_work ul li {
        font-size: 1.2rem;
    }
    .container {
        display: block;
        width: 100%;
        height: 100%;
        margin: 0;
        padding: 0;
    }
    .container .img {
        width: 100%;
        height: 100%;
        margin: 0;
        display: none;
    }
    .container .img img {
        width: 100%;
        height: 100%;
        margin: 0;
    }
    .container .content .link {
        font-size: 1rem;
        margin: 20px 10px;
    }
    .container .content p {
        font-size: 0.8rem;
        margin: 20px 0;
    }
       /* ============skills=========   */
    .skilles .content-box {
        display: inline-block;
    }
    .skilles .content-box .box{
        margin: 20px  0;
        padding: 0;
        font-size: 18px;
        width: 310px;
        height: 60px;
    }
    .skilles .content-box .box i{
        font-size: 25px;
    }
    .skilles .content-box-2 {
        display: inline-block;
    }
    .skilles .content-box-2 .box{
        margin: 20px 0;
        padding: 0;
        font-size: 18px;
        width: 310px;
        height: 60px;
    }
    .skilles .content-box-2 .box i{
        font-size: 35px;
    }
}
